.App {
  @apply text-center flex items-center justify-center p-5 w-full h-screen bg-sky-500 bg-gradient-to-r from-indigo-500 via-purple-500 to-pink-500 bg-300% animate-gradient;
}

.textField {
  @apply bg-sky-50 w-3/4 h-3/4 rounded-2xl opacity-70 flex justify-center items-center flex flex-col justify-between items-center
}

.icons {
  @apply text-3xl p-1 w-full flex justify-end items-center
}

.icon {
  @apply hover:scale-110 transition-all
}
